import { Component, OnInit, OnDestroy, Renderer2 } from '@angular/core';
import { Subscription } from 'rxjs';
import { LeftMenuService } from './../core/services/left-menu.service';

@Component({
  selector: 'root-comp',
  templateUrl: './root.component.html',
  styleUrls: ['./root.component.scss']
})
export class RootComponent implements OnInit, OnDestroy {
  private _observables: Subscription = new Subscription()

  constructor(
    private renderer: Renderer2,
    private leftMenuService: LeftMenuService
  ) { }

  ngOnInit(): void {
    if (window.innerWidth < 1024) {
      // Subscribe to the service who handle the state of the left menu - return a boolean
      this._observables.add(this.leftMenuService.openMenu$.subscribe((open: boolean) => {
          // Set / Unset class on the HTML DOM tag
          // Set overflow to hidden
          this.renderer[ open ? 'addClass' : 'removeClass' ](document.getElementsByTagName("html")[0], 'o-hidden');
        }
      ));
    }
  }

  ngOnDestroy(): void {
    // Unsubscribe
    this._observables.unsubscribe();
  }
}
